<template>
  <div class="iconos-shareIn">
    <social-sharing :url="url" inline-template>
      <div>
        <network network="facebook" class="span-network">
          <font-awesome-icon class="redes" :icon="['fab', 'facebook-f']" />
        </network>
        <network network="whatsapp" class="span-network">
          <font-awesome-icon class="redes" :icon="['fab', 'whatsapp']" />
        </network>
        <network network="twitter" class="span-network">
          <font-awesome-icon class="redes" :icon="['fab', 'twitter']" />
        </network>
        <network network="linkedin" class="span-network">
          <font-awesome-icon class="redes" :icon="['fab', 'linkedin']" />
        </network>
      </div>
    </social-sharing>
  </div>
</template>
<script>
export default {
  name: "ShareIn",
  data: () => ({
    routerPath: ""
  }),
  computed: {
    url() {
      const path =
        "http://192.168.0.101:8080" + this.$router.history.current.path;

      this.routerPath = path;

      //this.routerPath = 'https://github.com/nicolasbeauvais/vue-social-sharing'
      console.log(this.routerPath);
      return this.routerPath;
    }
  }
};
</script>
